<script>
	import 'chota';
	import {Container, Card, Modal, Tabs, Tab, Tag, Row, Col, Field, Input, Button} from "svelte-chota";
	import html2canvas from 'html2canvas';
	import * as bulksJSON from '../public/data/Bulks.json';
	import * as receiptLayoutsJSON from '../public/data/ReceiptLayouts.json';

	let bulks = bulksJSON.bulks;
	let receiptLayouts = receiptLayoutsJSON;

	let billBGs = [
		{class: "bill-bg1", name: "Bill BG 1"},
		{class: "bill-bg2", name: "Bill BG 2"},
		{class: "bill-bg3", name: "Bill BG 3"},
		{class: "bill-bg4", name: "Bill BG 4"},
		{class: "bill-bg5", name: "Bill BG 5"},
		{class: "bill-bg6", name: "Bill BG 6"},
		{class: "bill-bg7", name: "Bill BG 7"},
		{class: "bill-bg8", name: "Bill BG 8"},
		{class: "bill-bg9", name: "Bill BG 9"},
		{class: "bill-bg10", name: "Bill BG 10"},
		{class: "bill-bg11", name: "Bill BG 11"},
		{class: "bill-bg12", name: "Bill BG 12"},
		{class: "bill-bg13", name: "Bill BG 13"},
		{class: "bill-bg14", name: "Bill BG 14"},
		{class: "bill-bg15", name: "Bill BG 15"},
		{class: "bill-bg16", name: "Bill BG 16"}
	];

	let billFonts = [
		{class: "bill-font1", name: "Bill Font 1"},
		{class: "bill-font2", name: "Bill Font 2"},
		{class: "bill-font3", name: "Bill Font 3"},
		{class: "bill-font4", name: "Bill Font 4"},
		{class: "bill-font5", name: "Bill Font 5"}
	];

	let billTextVisibilities = [
		{class: "bill-text10", name: "Text Bright 10"},
		{class: "bill-text9", name: "Text Bright 9"},
		{class: "bill-text8", name: "Text Bright 8"},
		{class: "bill-text7", name: "Text Bright 7"},
		{class: "bill-text6", name: "Text Bright 6"},
		{class: "bill-text5", name: "Text Bright 5"},
		{class: "bill-text4", name: "Text Bright 4"},
		{class: "bill-text3", name: "Text Bright 3"},
		{class: "bill-text2", name: "Text Bright 2"},
		{class: "bill-text1", name: "Text Bright 1"}
	];

	let defaultLocation = '-- Choose Location --';
	let defaultBulkType = '-- Choose Bulk Type --';

	let selectedLocation;
	let selectedBulkType;
	let conditionalBulks = [];
	let selectedBulk;
	let selectedReceiptLayout;
	let currentReceipt = {};
	let selectedBillBG = billBGs[0];
	let selectedBillFont = billFonts[0];
	let selectedBillTextVisibility = billTextVisibilities[0];
	let activeTab = "generator";
	let addedReceipts = [];
	let addedReceiptCount = 0;
	let addedReceiptLoading = false;
	let originalReceipts = [];
	let originalReceiptCount = 0;
	let alertOpen = false;
	
	function getLocations() {
		let locations = [defaultLocation];
		bulks.forEach(e => {
			if(locations.indexOf(e.city) == -1) {
				locations.push(e.city);
			}
		});
		return locations;
	}
	let locations = getLocations();

	let locationBulksTypes = [defaultBulkType];
	function getBulksTypes() {
		locationBulksTypes = [defaultBulkType];
		bulks.forEach(e => {
			if(e.city == selectedLocation && locationBulksTypes.indexOf(e.type) == -1) {
				locationBulksTypes.push(e.type);
			}
		});
		conditionalBulks = [];
		selectedBulkType = locationBulksTypes[0];
		selectedBulk = null;
		selectedReceiptLayout = null;
	}

	function computeConditionalBulks() {
		conditionalBulks = [];
		bulks.forEach(e => {
			if(e.city == selectedLocation && e.type == selectedBulkType) {
				conditionalBulks.push(e);
			}
		});
		if(conditionalBulks.length > 0) {
			selectedBulk = conditionalBulks[0];
			changeReceiptLayout();
		}
	}

	function changeReceiptLayout() {
		currentReceipt = {};
		let receiptFields = receiptLayouts[selectedBulk.layout].layout;
		receiptFields.forEach(e => {
			if(e.type == 'option') {
				currentReceipt[e.name] = e.defaultValue[0];
			}
			else if(e.type == 'name') {
				currentReceipt[e.name] = selectedBulk.name;
			}
			else if(e.type == 'address' || e.type == 'thank') {
				currentReceipt[e.name] = selectedBulk[e.type];
				currentReceipt[e.name + '_orig'] = selectedBulk[e.type].split(',');
			}
			else if(e.type == 'welcome') {
				currentReceipt[e.name] = selectedBulk.welcome;
			}
			else if(e.type == 'line_datetime') {
				currentReceipt[e.name] = e.defaultValue;
				currentReceipt[e.name + '_date'] = e.defaultValue.split(' ')[0];
				currentReceipt[e.name + '_time'] = e.defaultValue.split(' ')[1];
			}
			else {
				currentReceipt[e.name] = e.defaultValue;
			}
		});
		selectedReceiptLayout = selectedBulk.layout;
		selectedBillFont = billFonts[selectedBulk.font - 1];
	}

	function computeVolumeByPrice(price) {
		let volumeName, totalName;
		receiptLayouts[selectedBulk.layout].layout.forEach(e => {
			if(e.type == 'volume') {
				volumeName = e.name;
			}
			else if(e.type == 'total') {
				totalName = e.name;
			}
		});
		currentReceipt[volumeName] = (currentReceipt[totalName] / price).toFixed(2);
	}

	function computeVolumeByTotal(total) {
		let volumeName, priceName;
		receiptLayouts[selectedBulk.layout].layout.forEach(e => {
			if(e.type == 'volume') {
				volumeName = e.name;
			}
			else if(e.type == 'price') {
				priceName = e.name;
			}
		});
		currentReceipt[volumeName] = (total / currentReceipt[priceName]).toFixed(2);
	}

	function handleLineDateTime(fieldName, value) {
		if(value && value.split(' ').length == 2) {
			currentReceipt[fieldName + '_date'] = value.split(' ')[0];
			currentReceipt[fieldName + '_time'] = value.split(' ')[1];
		}
	}

	function addReceipt() {
		if((addedReceipts.length + originalReceipts.length) == 4) {
			alertOpen = true;
		}
		else {
			addedReceiptLoading = true;
			let receiptDiv = document.getElementById("receiptDetails");
			addedReceipts.push(receiptDiv.outerHTML);
			addedReceiptCount = addedReceipts.length;
		}
	}

	function deleteReceipt(index) {
		addedReceipts.splice(index, 1);
		addedReceipts = addedReceipts;
		addedReceiptCount = addedReceipts.length;
	}

	function addOriginalReceipt() {
		if((addedReceipts.length + originalReceipts.length) == 4) {
			alertOpen = true;
		}
		else {
			document.getElementById('originalReceiptFile').click();
		}
	}

	function handleOriginalReceipt(e) {
		let image = e.target.files[0];
		let reader = new FileReader();
		reader.readAsDataURL(image);
		reader.onload = e => {
			originalReceipts.push(e.target.result);
			originalReceipts = originalReceipts;
			originalReceiptCount = originalReceipts.length;
		};
	}

	function deleteOriginalReceipt(index) {
		originalReceipts.splice(index, 1);
		originalReceipts = originalReceipts;
		originalReceiptCount = originalReceipts.length;
	}

	function downloadReceipt(){
		let receiptDiv = document.getElementById("receiptDetails");
		html2canvas(receiptDiv).then(function(canvas) {
			let fileName = selectedLocation + '_' + selectedBulkType + '_' + new Date().getTime() + '.png';
			downloadReceiptURI(canvas.toDataURL(), fileName);
		});
	}

	function downloadAllReceipts(){
		let receiptDiv = document.getElementById("allReceiptDetails");
		html2canvas(receiptDiv).then(function(canvas) {
			let fileName = 'All_Receipts_' + new Date().getTime() + '.png';
			downloadReceiptURI(canvas.toDataURL(), fileName);
		});
	}

	function downloadReceiptURI(uri, name) {
		var link = document.createElement("a");
		link.download = name;
		link.href = uri;
		document.body.appendChild(link);
		link.click();   
		document.body.removeChild(link);
	}
</script>

<main>
	<Container>
		<Modal bind:open={alertOpen}>
			<Card>
				<h4 slot="header">Oops!!!!</h4>
				Sorry!! You can add only 4 Receipts to Download all at Once.
				<div slot="footer" class="is-center">
					<Button class="receiptDownload" primary on:click={()=>{alertOpen = false}}>Ok</Button>
				</div>
			</Card>
		</Modal>
		<Modal bind:open={addedReceiptLoading}>
			<Card>
				<h4 slot="header">Receipt added Sucessfully</h4>
				Your Receipt added under Receipts Tab. 
				<br>You can add up to 4 Receipts and Download all at once.
				<div slot="footer" class="is-center">
					<Button class="receiptDownload" primary on:click={()=>{addedReceiptLoading = false}}>Ok</Button>
				</div>
			</Card>
		</Modal>
		<Row>
			<Col><h2>Fuel Bill Generator</h2></Col>
		</Row>
		<Row>
			<Col class="is-center">		
				<Tabs bind:active={activeTab}>
					<Tab tabid="generator">Bill Generator</Tab>
					<Tab tabid="receipts">
						Receipts 
						{#if addedReceiptCount > 0 || originalReceiptCount > 0}
							<Tag class="text-white bg-primary">{addedReceiptCount + originalReceiptCount}</Tag>
						{/if}
					</Tab>
				</Tabs>
			</Col>
		</Row>
		{#if activeTab == "generator"}
			<Row>
				<Col>
					<select bind:value={selectedLocation} on:change={getBulksTypes}>
						{#each locations as location}
							<option value={location}>
								{location}
							</option>
						{/each}
					</select>
				</Col>
				<Col>
					<select bind:value={selectedBulkType} on:change={computeConditionalBulks}>
						{#each locationBulksTypes as locationBulksType}
							<option value={locationBulksType}>
								{locationBulksType}
							</option>
						{/each}
					</select>
				</Col>
				<Col>
					{#if conditionalBulks.length > 0}
						<select bind:value={selectedBulk} on:change={changeReceiptLayout}>
							{#each conditionalBulks as conditionalBulk}
								<option value={conditionalBulk}>
									{conditionalBulk.name}
								</option>
							{/each}
						</select>
					{/if}
				</Col>
			</Row>
			{#if selectedReceiptLayout && currentReceipt}
			<Row>
				<Col size="9">
					<Card class="text-left">
						{#each receiptLayouts[selectedReceiptLayout].layout as receiptField}
						<Field gapless>
							{#if receiptField.type == 'option'}
								<Button dark>{receiptField.name}</Button>
								<select bind:value={currentReceipt[receiptField.name]}>
									{#each receiptField.defaultValue as optionValue}
										<option value={optionValue}>
											{optionValue}
										</option>
									{/each}
								</select>
							{:else if receiptField.type == 'address' || receiptField.type == 'thank'}
								<Button dark>{receiptField.name}</Button>
								<Input placeholder="{receiptField.defaultValue}" bind:value={currentReceipt[receiptField.name]} on:input={e => currentReceipt[receiptField.name + "_orig"] = e.target.value.split(",")}/>
							{:else if receiptField.type == 'line_datetime'}
								<Button dark>{receiptField.name}</Button>
								<Input placeholder="{receiptField.defaultValue}" bind:value={currentReceipt[receiptField.name]} on:input={(e) => {handleLineDateTime(receiptField.name, e.target.value)}}/>
							{:else}
								<Button dark>{receiptField.name}</Button>
								{#if receiptField.prefix}
									<Button outline>{receiptField.prefix}</Button>
								{/if}
								{#if receiptField.type == 'volume'}
									<Input disabled placeholder="{receiptField.defaultValue}" bind:value={currentReceipt[receiptField.name]}/>
								{:else if receiptField.type == 'price'}
									<Input placeholder="{receiptField.defaultValue}" bind:value={currentReceipt[receiptField.name]} on:input={e => computeVolumeByPrice(e.target.value)}/>
								{:else if receiptField.type == 'total'}
									<Input placeholder="{receiptField.defaultValue}" bind:value={currentReceipt[receiptField.name]} on:input={e => computeVolumeByTotal(e.target.value)}/>
								{:else}
									<Input placeholder="{receiptField.defaultValue}" bind:value={currentReceipt[receiptField.name]}/>
								{/if}
								{#if receiptField.suffix}
									<Button outline>{receiptField.suffix}</Button>
								{/if}
							{/if}
						</Field>
						{/each}
					</Card>
				</Col>
				<Col size="3">
					<select bind:value={selectedBillFont}>
						{#each billFonts as billFont}
							<option value={billFont}>
								{billFont.name}
							</option>
						{/each}
					</select>
					<select bind:value={selectedBillTextVisibility}>
						{#each billTextVisibilities as billTextVisibility}
							<option value={billTextVisibility}>
								{billTextVisibility.name}
							</option>
						{/each}
					</select>
					<select bind:value={selectedBillBG}>
						{#each billBGs as billBG}
							<option value={billBG}>
								{billBG.name}
							</option>
						{/each}
					</select>
					<Card class="text-left bill {selectedBillFont.class} {selectedBillBG.class}" id="receiptDetails">
						{#each receiptLayouts[selectedReceiptLayout].layout as receiptField}
							<Row class="{selectedBillTextVisibility.class}">
								{#if receiptField.type == 'logo'}
									<Col class="text-{receiptField.align}">
										<img class="bill-logo" src="{currentReceipt[receiptField.name]}" alt=""/>
									</Col>
								{:else if receiptField.type == 'address'}
									<Col class="text-{receiptField.align}" style="{receiptField.style}">
									{#each currentReceipt[receiptField.name + "_orig"] as addressLine}
										<Row>
											<Col style="{receiptField.style}">{addressLine}</Col>
										</Row>
									{/each}
									</Col>
								{:else if receiptField.type == 'thank'}
									<Col class="text-{receiptField.align}" style="{receiptField.style}">
									{#each currentReceipt[receiptField.name + "_orig"] as thankLine}
										<Row>
											<Col style="{receiptField.style}">{thankLine}</Col>
										</Row>
									{/each}
									</Col>
								{:else if receiptField.type == 'line_datetime'}
									<Col class="text-left" style="{receiptField.style}">
										{currentReceipt[receiptField.name + '_date']}
									</Col>
									<Col class="text-right" style="{receiptField.style}">
										{currentReceipt[receiptField.name + '_time']}
									</Col>
								{:else if receiptField.type == 'empty_line'}
									<Col class="text-left empty-line"></Col>
								{:else}
									{#if receiptField.columns == 2}
									<Col size="{receiptField.width}" class="text-left" style="{receiptField.style}">
										{receiptField.name}:
									</Col>
									{:else if receiptField.columns > 2}
									<Col size="{receiptField.width}" class="text-left" style="{receiptField.style}">
										{receiptField.name}
									</Col>
									<Col size="1" class="text-left is-marginless" style="{receiptField.style}">:</Col>
									{/if}
									<Col class="text-{receiptField.align}" style="{receiptField.style}">
										{#if receiptField.prefix}{receiptField.prefix}{/if}{currentReceipt[receiptField.name]}{#if receiptField.suffix}{receiptField.suffix}{/if}
									</Col>
								{/if}
							</Row>
						{/each}
					</Card>
					<Row>
						<Col size="6"><Button class="receiptDownload" secondary on:click={addReceipt}>Add Receipt</Button></Col>
						<Col size="6"><Button class="receiptDownload" primary on:click={downloadReceipt}>Download</Button></Col>
					</Row>
				</Col>
			</Row>
			{/if}
		{/if}
		{#if activeTab == "receipts"}
			<Container>
				<Button class="addOriginalReceipt" secondary on:click={addOriginalReceipt}>Add Original Receipt</Button>
				<input id="originalReceiptFile" style="display:none" type="file" accept=".jpg, .jpeg, .png" on:change={(e)=>handleOriginalReceipt(e)}/>
				{#if addedReceipts.length > 0 || originalReceipts.length > 0}
				<Button class="receiptDownload" primary on:click={downloadAllReceipts}>Download All Receipts</Button>
				{/if}
			</Container>
			{#if addedReceipts.length > 0 || originalReceipts.length > 0}
			<Card id="allReceiptDetails">
				<Row>
					{#each addedReceipts as receiptHtml}
						<Col size="3" class="is-full-width">
							{@html receiptHtml}
						</Col>
					{/each}
					{#each originalReceipts as originalReceipt}
						<Col size="3" class="is-full-width">
							<img src="{originalReceipt}" alt="d"/>
						</Col>
					{/each}
				</Row>
			</Card>
			<Card>
				<Row>
					{#each addedReceipts as receipt, index}
						<Col size="3" class="text-center">
							<Button value="{index}" error on:click={e => deleteReceipt(e.target.value)}>Delete</Button>
						</Col>
					{/each}
					{#each originalReceipts as originalReceipt, index}
						<Col size="3" class="text-center">
							<Button value="{index}" error on:click={e => deleteOriginalReceipt(e.target.value)}>Delete</Button>
						</Col>
					{/each}
				</Row>
			</Card>
			{:else}
			<Card>
				<h3>No Receipts added yet!!</h3>
			</Card>
			{/if}
		{/if}
	</Container>
</main>

<style>
	main {
		text-align: center;
		padding: 1em;
		max-width: 240px;
		margin: 0 auto;
	}

	h2 {
		color: #171718;
		font-size: 3em;
		font-weight: bold;
	}

	@media (min-width: 640px) {
		main {
			max-width: none;
		}
	}
</style>